@import "../../../styles/breakpoints.scss";


.host {
  position: relative;
  //height: 100vh;

  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: stretch;
  flex: auto;

  @media (--app-viewport-xsmall) {
    flex-direction: column;
  }

  @media (--app-viewport-small) {
    flex-direction: column;
  }

  @media (--app-viewport-medium) {
    flex-direction: row;
  }

  @media (--app-viewport-large) {
    flex-direction: row;
  }

  &[app-custom-media-show="show"] {
    @media (--app-viewport-xsmall) {
      background-color: var(--error);
    }

    @media (--app-viewport-small) {
      background-color: var(--success);
    }

    @media (--app-viewport-medium) {
      background-color: var(--attention);
    }

    @media (--app-viewport-large) {
      background-color: var(--notice);
    }
  }
}

.firstContainer {
  align-self: center;
  display: flex;
  flex: auto;
  flex-direction: column;
  }

.headerTopBarContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  font-family: 'Montserrat';
  font-size: 20px;
  color: #424242;
  padding-top: 100px;
}

.firstContainerContent {
  align-self: center;
  display: flex;
  flex-direction: column;
  
  align-items: center;
  width: 23%;
  height: 400px;
  min-width: 250px;
  max-width: 300px;
  border: 1px solid var(--app-color-gray-lighter);
  padding: 10px 20px 20px 20px;
  margin: 10px 30px 10px 30px
}

.logoWrapper {
  align-self: flex-start;
  margin-top: 15px;
  margin-bottom: 20%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.processmakerLogo {
  width: 182px;
  height: 32px;
}

.header2 {
  align-self: flex-start;
  color: var(--app-color-text-darker);
  font-family: var(--app-text-main-font);
  font-size: 32px;
  font-weight: 500;
  letter-spacing: 0;
  margin: 0;
}

.recoverPasswordButton {
  //align-self: flex-end;
  align-items: center;
  text-decoration: underline;
  text-underline-offset: 3px;
}

.secondContainer {
  width: 50%;
  padding-left: 40px;

  width: 534px;
  height: 100%;
  flex: auto;
  background-color: var(--app-color-bg-default);
}

.bottomButtonsContainer {
  display: flex;
  width: 100%;
  flex: 1;
  justify-content: space-between;
  margin-left: -60px;
}

.headerTopBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
}

.logoUrlImg {
  object-fit:scale-down;
  height: 100px;
  width: 220px;
  margin: 5px;
}

.centerDiv {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 100%;
}

.logoUrlImgMobile {
  object-fit:scale-down;
  height: 80px;
  width: 220px;
  margin: 5px;
}